import Layout from '../layout/index.vue'
import LoginRegister from '../views/login/index.vue'

import Home from '../views/main/home/index.vue'
import Articles from '../views/main/articles/index.vue'
import ArticleDetail from '../views/main/articles/detail/index.vue'
import Messages from '../views/main/messages/index.vue'
import Ramblings from '../views/main/ramblings/index.vue'
import Settings from '../views/main/settings/index.vue'

export const myRoutes = [
  {
    path: '/',
    redirect: '/home',
    name: 'myLayout',
    component: Layout,
    meta: {
      title: '',
      hidden: false,
      requireLoginAuth: false,
    },
    children: [
      {
        path: 'home',
        name: 'myHome',
        component: Home,
        meta: {
          title: '',
          hidden: false,
        },
      },
      {
        path: 'articles',
        name: 'myArticles',
        component: Articles,
        meta: {
          title: '',
          hidden: false,
        },
      },
      {
        path: 'articledetail',
        name: 'myArticleDetail',
        component: ArticleDetail,
        meta: {
          title: '',
          hidden: true,
        },
      },

      {
        path: 'ramblings',
        name: 'myRamblings',
        component: Ramblings,
        meta: {
          title: '',
          hidden: false,
          // requireLoginAuth: true,
        },
      },
      {
        path: 'messages',
        name: 'myMessages',
        component: Messages,
        meta: {
          title: '',
          hidden: false,
        },
      },
      {
        path: 'settings',
        name: 'mySettings',
        component: Settings,
        meta: {
          title: '',
          hidden: true,
          requireLoginAuth: true,
        },
      },
    ]
  },

  {
    path: '/login',
    name: 'myLoginRegister',
    component: LoginRegister,
    requireLoginAuth: false,
  },

]